    import { mapState, mapGetters } from "vuex";

    export default {
        data() {
            return {
		        success: false,
		        lose: false,
		        _num: 0,
		        more: false,
		        setting: {},
		        coupon: [],
		        userInfo: {
		            integral: 0
		        },
		        choose: {
		            integral: 0
		        },
		        banners: [],
		        cats: [],
		        id: 0,
		        exchangeCoupon: false,
		        current: 'wechat',
		        goods: [],
		        args: false,
		        page: 1,
				disable: 'disable'
            }
        },
        computed: {
            ...mapState({
                integralImg: state => state.mallConfig.plugin.integral_mall,
                appSetting: state => state.mallConfig.mall.setting,
                appImg: state => state.mallConfig.__wxapp_img.mall,
                platform: function(state) {
                    return state.gConfig.systemInfo.platform;
                }
            }),
             ...mapGetters('mallConfig', {
				getVideo: 'getVideo',
			 	getTheme: 'getTheme'
            }),
        },
        methods: {
            close() {
                this.success = false;
                this.lose = false;
            },

        	tab(id) {
        		this._num = id;
        		this.page = 1;
        		this.goods = [];
        		this.getGoods();
        	},

            toList() {
                this.success = false;
                uni.navigateTo({
                    url: '/pages/coupon/index/index'
                });
            },
            toGoods(data) {

                if (data.goodsWarehouse.video_url && this.getVideo == 1) {

                    uni.navigateTo({
                        url: `/pages/goods/video?goods_id=${data.id}&sign=${data.sign}`
                    });






                } else {
                    uni.navigateTo({
                        url: '/plugins/integral_mall/goods/goods?goods_id=' + data.id
                    });
                }








            },
            toAbout() {
				uni.navigateTo({
					url: `/pages/rules/index?url=${encodeURIComponent(this.$api.integral_mall.index)}&keys=${JSON.stringify(['setting', 'rule'])}`,
				});
            },
			router(url) {
				uni.navigateTo({
					url: url
				});
			},
            getList() {
                let that = this;
                that.$request({
                    url: that.$api.integral_mall.index,
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code === 0) {
                    	let { banners, setting, userInfo } = response.data;
                    	that.banners = banners;
		                that.setting = setting;
		                that.userInfo = userInfo;
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(() => {
                    that.$hideLoading();
                });
            },
            getCoupon() {
                let that = this;
                that.$request({
                    url: that.$api.integral_mall.coupon,
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code === 0) {
                    	that.coupon = response.data.list;
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(() => {
                    that.$hideLoading();
                });
            },
            getCat() {
                let that = this;
                that.$request({
                    url: that.$api.integral_mall.cats,
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code === 0) {
                    	that.cats = response.data.list;
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(() => {
                    that.$hideLoading();
                });
            },
            getGoods() {
                let that = this;
                that.$request({
                    url: that.$api.integral_mall.goods,
                    data: {
		                cat_id: that._num,
		                page: that.page
		            }
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code === 0) {
                    	that.more = false;
                    	if(response.data.list.length == response.data.pagination.pageSize) {
                    		that.page++;
                    		that.more = true;
                    	}
                    	if(that.page > 1) {
                    		that.goods = that.goods.concat(response.data.list);
                    	}else {
                    		that.goods = response.data.list;
                    	}
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(() => {
                    that.$hideLoading();
                });
            },
        },
        onReachBottom() {
        	if(this.more) {
            	this.getGoods();
        	}
        },

        onShareAppMessage() {
            return this.$shareAppMessage({
                title: this.$children[0].navigationBarTitle,
                path: "/plugins/integral_mall/index/index",
            });
        },


        onShareTimeline() {
          // 分享朋友圈beta
          return this.$shareTimeline({
            title: this.$children[0].navigationBarTitle,
            query: {} // 此处填写页面的参数
          });
        },


        onLoad() { this.$commonLoad.onload();
            let that = this;
            that._num = 0;

            wx.showShareMenu({
              menus: ['shareAppMessage', 'shareTimeline']
            })

            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
	        that.getList();
	        that.getCoupon();
	        that.getCat();
	        that.getGoods();
        }
    }
